@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

$line-color: rgba(132, 142, 179, 0.2);

@mixin planet-container($height, $width) {

    border: 1px solid $line-color;
    border-radius: 50%;
    height: $height;
    width: $width;
    position: relative;

    @media (min-width: 1000px) {
      left: 30px;
      top: 30px;
    }

    @media (min-width: 1500px) {
      left: 45px;
      top: 45px;
    }

    @media(min-width: 400px) and (max-width: 699px) {
      left: 32px;
      top: 32px;
    }
}

@mixin fonts() {
  display: flex;
  align-items: center;
  flex-direction: column;
  p {
    padding: .5rem;
    font-family: 'Raleway', sans-serif;
  }
}

@mixin planet-body($height, $width, $color, $top, $left) {
  content: '';
  position: absolute;
  height: $height;
  width: $width;
  background-color: $color;
  border-radius: 50%;
  top: $top;
  left: $left;
}

@mixin planet-click($color) {
  border-radius: 50%;
  position: relative;
  height: 80px;
  width: 80px;
  background: $color;
  animation: none;
}

@mixin planet-hover() {
  &:hover:not(.selected) {
    box-shadow: 0 0 30px white;
    cursor: pointer;
  }
}

.Container {
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: rgb(58,75,142);
  background: radial-gradient(circle, rgba(58,75,142,1) 0%, rgb(5, 7, 15) 35%, rgba(6,11,30,1) 100%);
  position: absolute;
  display: flex;
  justify-content: center;

  .neptune {
    margin-top: 27px;
    border: 1px solid $line-color;
    border-radius: 50%;
    height: 90vh;
    width: 90vh;
    position: relative;
    animation: orbit infinite 60s linear;
    &::before {
      @include planet-body(40px, 40px, #3575BF, -22px, 50% );
    }

    @keyframes orbit {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }

    .uranus {
      @include planet-container( 80vh, 80vh );
      &::before {
        @include planet-body(25px, 25px, #449BFD, -12px, 50% );
      }
      animation: orbit infinite 55s linear;
      .saturn {
        @include planet-container( 70vh, 70vh );
        animation: orbit infinite 50s linear;
        &::before {
          @include planet-body(20px, 20px, #E3F318, -10px, 50% );
        }
        .jupiter {
          @include planet-container( 60vh, 60vh );
          animation: orbit infinite 49s linear;
        &::before {
          @include planet-body(15px, 15px, #ED8100, -8px, 50% );
        }
          .mars {
            @include planet-container( 50vh, 50vh );
            animation: orbit infinite 40s linear;
        &::before {
          @include planet-body(7px, 7px, #F37138, -4px, 50% );
        }
            .earth {
              @include planet-container( 40vh, 40vh );
              animation: orbit infinite 45s linear;
        &::before {
          @include planet-body(10px, 10px, #5B79F2, -4px, 50% );
        }
              .venus{ 
                @include planet-container( 30vh, 30vh );
                animation: orbit infinite 50s linear;
        &::before {
          @include planet-body(10px, 10px, #E6EE6D, -5px, 50% );
        }
                .mercury {
                  @include planet-container( 20vh, 20vh );
                  animation: orbit infinite 10s linear;
                  &::before {
                    @include planet-body(10px, 10px, #D3D3D3, -4px, 50% );
                  }
                  
                }
              }
            }
          }
        }
      }
    }
  }
    .sun {
      border-radius: 50%;
      background-color: rgb(229, 230, 203);
      height: 40px;
      width: 40px;
      position: relative;
      top: 46%;
      right: 24%;  
      box-shadow: 0 0 30px white;
      @media (max-width: 800px) {
        top: 49%;
        right: 24%;
      }
      @media (max-width: 600px) {
        top: 49%;
        right: 30%;
      }
      @media (max-width: 500px) {
        right: 30%;
      }
    }

    .planets-container {
      position: absolute;
      left: 0;
      width: 300px;
      height: 50%;
      padding: 1rem;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      .neptune-planet {
        @include planet-hover();
        @include planet-click(#407af7);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(34,63,125,1);
        }
      }
      .uranus-planet {
        @include planet-hover();
        @include planet-click(#9DB1BF);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(86, 102, 112, 1);
        }
      }
      .saturn-planet {
        @include planet-hover();
        @include planet-click(#D1AA5E);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(122, 99, 54,1);
        }
        &::after {
          content: '';
          background: transparent;
          border-top: none;
          border-bottom: 1px solid #a98236;
          border-left: 10px solid #a98236;
          height: 23px;
          left: -18px;
          top: 48px;
          width: 70px;
          position: absolute;
          border-radius: 50%;
          transform: rotate(-23deg) skew(-12deg, -9deg);
        }
        &::before {
          content: '';
          background: transparent;
          border-bottom: none;
          border-right: 14px solid #a98236;
          border-top: 5px solid #a98236;
          height: 31px;
          left: -10px;
          top: 16px;
          width: 97px;
          transform: scaleX(-1) skew(0deg, 8deg) rotate(222deg);
          position: absolute;
          border-radius: 50%;
        }
      }
      .jupiter-planet {
        @include planet-hover();
        @include planet-click(linear-gradient(#E89150 5px, #ffc382 7px, #D16F1B 18px, #ffc382 20px, #D16F1B 23px, #ffc382 40px, #E89150 75px));
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(122, 99, 54, .5);
        }
      }
      .mars-planet {
        @include planet-hover();
        @include planet-click(#C25447);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(102, 44, 37,1);
        }
      }
      .earth-planet {
        @include planet-hover();
        @include planet-click(#496282);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(41, 56, 74,1);
        }
      }
      .venus-planet {
        @include planet-hover();
        @include planet-click(linear-gradient(#e5875f 5px, #c2643c 7px, #D1734B 18px, #ea8c64 20px, #c76941 25px));
        transform: rotate(45deg);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(145, 73, 42, .5);
        }
      }
      .mercury-planet {
        @include planet-hover();
        @include planet-click(#76777A);
        &:not(.selected) {
          box-shadow: inset 11px 10px 9px -4px rgba(54, 56, 61, 1);
        }
      }
    }

    .card-container {
      position: absolute;
      right: 10px;
      top: 10px;
      // max-width: 220px;
      min-width: 350px;
      height: auto;
      border-radius: 10px 2px 2px 10px;
      background-color: #407076;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 2px;
      }

      &::-webkit-scrollbar-track {
        background: $line-color;
      }

      &::-webkit-scrollbar-thumb {
        background: #888;
      }

      .card-head {
        img {
          min-width: 350px;
          border-radius: 10px 0 0 0;
        }
      }

      .card-body {
        padding: .5rem;
        ul li {
          font-family: 'Raleway', sans-serif;
        }
        h3 {
          font-family: 'Raleway', sans-serif;
          margin: 0;
        }
      }
      .info-panel, .loader {
        @include fonts();
        img {
          width: 300px;
        }
      }
    }

    .selected {
      box-shadow: 0 0 30px white;
      cursor: pointer;
    }
    .highlight {
      &::before {
        box-shadow: 0 0 56px 15px white;
      }
    }
}
